* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body,
#content {
    height: 100%;
    width: 100%;
}

body {
    display: flex;
    flex-direction: column;
    background-color: #000;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: 100% 100%;
    background-repeat: no-repeat;
    color: #fff;
    font-family: var(--ff-primary);
    box-shadow: 1px 1px 10vw 10vw black inset,
        -1px -1px 10vw 10vw black inset;
}

h1,
h2,
h3 {
    font-family: var(--ff-secondary);
}

h1 {
    font-size: 2rem;
    font-weight: 900;
}

a {
    text-decoration: none;
    color: inherit;
}

button {
    all: unset;
    padding: 1.5em 2em .5em 2em;
    margin-top: auto;
    font-family: var(--ff-secondary);
    text-transform: uppercase;
    font-size: var(--fs1);
    cursor: pointer;
}

nav {
    display: grid;
    grid-template-columns: 15% 15% 25% 15% 15%;
    justify-content: center;
    justify-items: center;
}

nav > span {
    margin-top: auto;
    padding: 1.5em 2em .5em 2em;
}

@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

nav > button:hover {
    border-bottom: 5px solid var(--clr-orange);
    margin-bottom: -5px;
    transition: .2s ease border-bottom;
}

nav > button:active {
    color: var(--clr-orange);
}

.home,
.menu,
.about,
.reservation {
    display: flex;
    flex-direction: column;
    height: 100%;
    align-items: center;
    justify-content: center;
    animation: fadeInAnimation ease 3s;
}

.home {
    letter-spacing: .2em;
}

.home .welcome {
    font-size: 4rem;
    margin: 0;
}

.home h1 {
    font-size: 6rem;
    margin-top: -25px;
    margin-bottom: 25px;
    font-weight: 600;
    text-shadow: 0 0 2px rgba(0, 0, 0, .8);
}

.home .tagline,
.home .welcome {
