}

h2 {
    font-size: var(--fs3);
    text-transform: uppercase;
    letter-spacing: .1em;
}

h3 {
    font-size: var(--fs2);
    font-weight: 500;
}

.reservation,
.about,
.menu {
    gap: var(--s2);
    margin: 0 auto;
    background-color: rgba(0, 0, 0, .5);
}

.blurb,
.menu-item,
.resForm {
    display: flex;
