    box-shadow: 0 0 8px rgba(255, 255, 255, 0.3);
}

.resForm button {
    outline: 1px solid #fff;
    padding: .75em 1.5em;
    margin-top: var(--s1);
    text-shadow: 3px 3px 5px black;
}

.resForm button:hover {
    font-weight: 500;
    box-shadow: 0 0 30px rgba(255, 255, 255, 0.3);
    transition: .2s ease-in-out all;
